import PropTypes from "prop-types";
import {Link} from "react-router-dom";
import styles from "../css/Movie.module.css";

function Movie({id, medium_cover_image, title, summary, genres, year}) {
    return (
        <div className={styles.movie}>
            <img src={medium_cover_image} alt={title} className={styles.movie__img}/>
            <h1 className={styles.movie__title}>
                <Link to={`/movie/${id}`}>{title}</Link>
            </h1>
            <h3 className={styles.movie__year}>{year}</h3>
            <p>{summary}</p>
            <ul className={styles.movie__genres}>
                {genres.map(g => (
                    <li key={g}>{g}</li>
                ))}
            </ul>
        </div>
    )
}

Movie.propTypes = {
    id: PropTypes.number.isRequired,
    year: PropTypes.string.isRequired,
    medium_cover_image: PropTypes.string.isRequired,
    title: PropTypes.string.isRequired,
    summary: PropTypes.string.isRequired,
    geners: PropTypes.arrayOf(PropTypes.string).isRequired,
};

export default Movie;